@import './normalize.scss';

// === Fonts >>
@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
// === Fonts <<

// === Colors >>
$backgroundColor: #2ed573;
$appBackgroundColor: #4834d4;
$mainFontColor: #ffffff;
// === Colors <<




.clock {
    &-outer {
        width: 100%;
        height: 100vh;
        background-color: $backgroundColor;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 5px;
    }

    &-inner {
        max-width: 620px;
        width: 100%;
        height: auto;
        background-color: $appBackgroundColor;
        border-radius: 6px;
        font-family: 'Roboto', sans-serif;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 5px;
        user-select: none;
        
        &-appName {
            font-size: 36px;
            color: $mainFontColor;
            margin-bottom: 20px;

        }

        &-setup {
            display: flex;
            justify-content: space-around;
            font-size: 24px;
            width: 100%;
            color: $mainFontColor;

            
            &-break {
                &-text {

                }
                &-change {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    &-time {
                        width: 40px;
                        text-align: center;
                    }

                    &-timeUp, &-timeDown {
                        cursor: pointer;
                    }
                }   
            }

            &-session {
    

                &-change {
                    display: flex;
                    justify-content: center;

                    &-time {
                        width: 40px;
                        text-align: center;
                    }
    
                    &-timeUp, &-timeDown {
                        cursor: pointer;
                    }
                }
            }
        }

        &-display {
            border: 4px solid #94888a;
            border-radius: 20px;
            padding: 20px;
            margin-top: 20px;
            margin-bottom: 10px;
            width: 200px;
            &-sessionText {
                text-align: center;
                font-size: 24px;
            }

            &-sessionTime {
                display: flex;
                justify-content: center;
                
                font-size: 56px;
                text-align: center;
            }
        }

        &-controller {
            display: flex;
            flex-direction: column;
            align-items: center;

            &-wrapper {
                display: flex;
                max-width: 60px;
                width: 100%;
                justify-content: space-around;
                color: $mainFontColor;
                margin-bottom: 10px;
                
                &-play,&-pause,&-stop {
                    cursor: pointer;
                }
            }
        }

        &-author {
            font-size: 10px;
            color:  #ff6b81;
        }
    }
}